import { Value } from '@ali/4ever-cangjie';
import Table from '../../mo/models';
import { isTable } from '../types';

const getTable = (value: Value): Table | null => {
  const { document, selection } = value;
  const table = document.getClosest(selection.getStart(document).key, isTable) as Table || null;
  return table;
};

export default getTable;
